<template>

<div class="jumbotron">

<div class="Q">

 <h5 class="mb-5">{{ current.question }}</h5>

 <img :src="getIconPath(current.image)" class="w-50"/></div>

<div class="R m-5">

<ul class="list-group mb-5">

<li class="list-group-item p-3" v-for="(ans,index) in answer" :key="index" @click="select(index)" @dblclick="unselect"

 :class="[selectedIndex === index ? 'selected':
          // answered && answer[index]===current.correct_answer ? 'green':
          // answered && selectedIndex === index && answer[index]!==current.correct_answer ? 'red':
           
           ''          
           ]"> {{ ans }}</li>

</ul>
    

<button class="btn btn-success" :disabled="selectedIndex==null" @click="valid">Valider</button>
<button class="btn btn-secondary text-white ml-2" @click="next">Suivant</button> 

</div>

</div>



</template>




<script>

export default {

  props: {

    current:Object,
    next:Function,
    increment:Function,
    i:Number,
    q:Array
  },

  data() {
     
     return {

      selectedIndex:null,
       answered:false

     }


  },

  watch:{
       
       current(){

         this.selectedIndex=null

       }

  },

  computed: {
    
    answer: function(){
        
        var tb;
        
         tb=this.current.incorrect_answer;

         tb.push(this.current.correct_answer);

       var temp,n,m;

  for(var i=0;i<tb.length;i++){

     n=Math.floor(Math.random()*tb.length);
     m=Math.floor(Math.random()*tb.length);
     
     if(n==m){

       continue;
     }
     else{
           
          temp=tb[n];
          tb[n]=tb[m];
          tb[m]=temp;

          

   }
   
  }
   return tb;

    }

  },

 methods : {

select : function(index){

this.selectedIndex=index;



},


unselect : function(){

this.selectedIndex=null;



},

getIconPath(image){
     
     return require("../assets/images/"+image)

},

valid: function(){
  
  var iscorrect=false;

if(this.answer[this.selectedIndex]==this.current.correct_answer){

      iscorrect=true;
      this.answered=true;     
      
}


    

this.increment(iscorrect);

},



 },

  }

</script>

<style scoped>

.list-group-item:hover{

background:#41b883;
opacity:0.8;
color:white;

}

.list-group-item{

cursor:pointer;

}

.selected{

background:#6c757d;
color:white;

}

.green{

background:#41b883;
opacity:0.8;
color:white;

}


.red{
  
background:#dd2a2a;
opacity:0.8;
color:white;

}


*{
   user-select: none;
   transition:0.1s;
}

</style>