               }

           },
        increment: function(iscorrect){

            if(iscorrect){
                  this.score++; 
            }
            this.next();    
        },

        redoo(){
          
           window.location.reload();

        },

        fin(tab,m,n){
              
              tab.splice(n,0,tab.splice(m,1)[0]);
                
             return tab;
        }

        
  },

  computed:{

  realIndex (){

       var i=this.index+1;

       if(this.index<this.questions.length){

            return i;

       }

       else {

         return 0;
       }

          
  },

  questions (){

    var q=this.question,
        n,tab=[];

    while(tab.length!=10){
        
        n=Math.floor(Math.random()*40);

        if(q[n]){
         
          tab.push(q[n]);
          this.fin(q,n,q.length);
          q.pop();

        }
 

    }

     return tab;    

  }

