<template>

<div>
<center>
          {{percent}}
<div class="progress bg-secondary w-50">
   
<div class="progress-bar progress-bar-animated bg-success" :style="val">
   
</div> 

</div> </center>

<br>


</div>    

</template>

<script>

export default {
    
    props:['next','index','percent'],

    watch:{
         
        index(){

     setInterval(() => {

                 if(this.percent!=100){

              this.percent+=1;
          }
           else{
             
                this.next();
                this.percent=0;
               

               }

                
            },1000);

          
               

        }

 
    },

    computed : {
         
         val(){

             var a= "width :"+this.percent+"%";

             return a;

         }


    },

}

</script>